argin:0}.blurb a{transition:color 0.3s;color:#dbbfb0;text-decoration:none}.blurb a:hover{color:white}.project{height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;color:#191819;flex-direction:row;cursor:pointer}.project img{width:auto;height:75%;z-index:0}@media screen and (max-width: 600px){.project img{height:50%;width:auto}}.project:nth-of-type(odd){flex-direction:row-reverse}@media screen and (max-width: 600px){.project{flex-direction:column}.project:nth-of-type(odd){flex-direction:column}}.project .project-content{max-width:400px;padding:32px;z-index:2;color:white}.project .project-content h1{margin:0 0 0 -2px;font-weight:500}.project .project-content h4{font-variation-settings:'wght' 400;font-weight:normal;font-size:16px;margin:0;color:rgba(255,255,255,0.5)}.project .project-content h4 span{color:white}.project .project-content h5{text-transform:uppercase;font-size:0px;font-variation-settings:'wght' 800;margin-bottom:-8px;letter-spacing:1px}.project .project-content h6{font-size:10px;font-variation-settings:'wght' 500;font-weight:500;margin:-8px 0 16px 0}.project .illustration-hero{height:20%;width:auto}@media screen and (max-width: 600px){.project .illustration-hero{height:auto;width:90%}}.project .recall-hero{height:40%;width:auto}@media screen and (max-width: 600px){.project .recall-hero{height:auto;width:90%}}@media screen and (max-width: 600px){.project .fabric-hero.fabric-hero{height:auto;width:120%}}

